import { useState } from "react";
import logoImg from "../res/imgs/Logo.png";
import arrowImg from "../res/imgs/right-arrow.png";

export default function Login() {
  const [data, setData] = useState({
    email: "",
    pass: "",
  });

  const submitHandler = async (e) => {
    e.preventDefault();

    
  };

  return (
    <form
      onSubmit={submitHandler}
      className="flex flex-col w-1/3 mx-auto mt-[10%] bg-[#64a51b]  text-white rounded-md overflow-hidden p-4"
    >
      <h1 className="text-2xl text-center font-semibold my-10">
        <img src={logoImg} alt="LinkedCord" width="75px" className="mx-auto" />
        <span>Admin Panel</span>
      </h1>
      <div className="flex flex-col">
        <label htmlFor="email">Your Email:</label>
        <input
          type="email"
          id="email"
          value={data.email}
          onChange={(e) => setData({ ...data, email: e.target.value })}
          className="rounded-md p-1 pl-3 text-[#64a51b]"
          placeholder="johndoe@gmail.com"
          required={true}
        />
      </div>

      <div className="flex flex-col mt-4">
        <label htmlFor="password">Your Password:</label>
        <input
          type="text"
          id="password"
          value={data.pass}
          onChange={(e) => setData({ ...data, pass: e.target.value })}
          className="rounded-md p-1 pl-3 text-[#64a51b]"
          placeholder="********"
          required={true}
        />
      </div>

      <button className="my-8 bg-white text-[#64a51b] rounded-md p-1 flex flex-row items-center justify-center hover:shadow-xl">
        <span>Proceed to Login</span>
        <img src={arrowImg} alt="arrow" width="15px" className="ml-4" />
      </button>
    </form>
  );
}
