import { NavLink } from "react-router-dom";
import logoImg from "../res/imgs/Logo.png";
import dashboardImg from "../res/imgs/dashboard.png";
import usersImg from "../res/imgs/users.png";
import walletImg from "../res/imgs/wallet.png";
import processImg from "../res/imgs/process.png";
import logoutImg from "../res/imgs/logout.png";
import { useLayoutEffect } from "react";

export default function Sidebar() {
  const isAuthenticated = true;

  useLayoutEffect(() => {
    if (!isAuthenticated) window.location.replace("/login");
  }, []);

  return (
    <div className="w-[350px] min-h-screen bg-[#76C31C] p-4">
      <div className="">
        <NavLink to="/" className="flex items-center justify-start">
          <img src={logoImg} alt="LinkedCord" width="75px" />{" "}
          <span className="text-white text-2xl font-semibold">LinkedCord</span>
        </NavLink>
      </div>

      <nav className="mt-20">
        <NavLink
          to="/"
          className={({ isActive }) =>
            isActive ? "active-nav nav-item" : "nav-item"
          }
        >
          <img
            src={dashboardImg}
            alt="Dashboard"
            width="25px"
            className="mr-2"
          />
          <span>Dashboard</span>
        </NavLink>
        <NavLink
          to="/user"
          className={({ isActive }) =>
            isActive ? "active-nav nav-item" : "nav-item"
          }
        >
          <img
            src={usersImg}
            alt="User Management"
            width="25px"
            className="mr-2"
          />
          <span>User Management</span>
        </NavLink>
        <NavLink
          to="/payment"
          className={({ isActive }) =>
            isActive ? "active-nav nav-item" : "nav-item"
          }
        >
          <img
            src={walletImg}
            alt="Payment Management"
            width="25px"
            className="mr-2"
          />
          <span>Payment Management</span>
        </NavLink>
        <NavLink
          to="/history"
          className={({ isActive }) =>
            isActive ? "active-nav nav-item" : "nav-item"
          }
        >
          <img
            src={processImg}
            alt="History Management"
            width="25px"
            className="mr-2"
          />
          <span>History Management</span>
        </NavLink>
        <NavLink
          to="/logout"
          className={({ isActive }) =>
            isActive ? "active-nav nav-item" : "nav-item"
          }
        >
          <img src={logoutImg} alt="Logout" width="25px" className="mr-2" />
          <span>Logout?</span>
        </NavLink>
      </nav>
    </div>
  );
}
