import Sidebar from "../components/Sidebar";
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
} from "recharts";
import { useEffect, useState } from "react";
import axios from "axios";

import userImg from "../res/imgs/user.png";
import uptrendImg from "../res/imgs/uptrend.png";
import prohibitionImg from "../res/imgs/prohibition.png";
import campaignImg from "../res/imgs/campaign.png";

export default function Dashboard() {
  return (
    <section className="flex">
      <Sidebar />
      <div className="w-full">
        <div className="container">
          <Content />
        </div>
      </div>
    </section>
  );
}

function Content() {
  return (
    <div className="mt-20">
      <div className="p-4 flex flex-row items-center justify-between text-white">
        <div className="w-1/4 rounded-md mx-2 flex items-center overflow-hidden border border-[#64a51b] text-[#64a51b]">
          <span className="w-16 bg-[#64a51b] h-12">
            <img
              src={userImg}
              height="30px"
              width="30px"
              className="animate-pulse mx-auto mt-2"
              alt="user"
            />
          </span>
          <span className="ml-2">
            357 <br />
            Total User
          </span>
        </div>
        <div className="w-1/4 rounded-md mx-2 flex items-center overflow-hidden border border-[#64a51b] text-[#64a51b]">
          <span className="w-16 bg-[#64a51b] h-12">
            <img
              src={prohibitionImg}
              height="30px"
              width="30px"
              className="animate-pulse mx-auto mt-2"
              alt="block"
            />
          </span>
          <span className="ml-2">
            0 <br />
            Blocked User
          </span>
        </div>
        <div className="w-1/4 rounded-md mx-2 flex items-center overflow-hidden border border-[#64a51b] text-[#64a51b]">
          <span className="w-16 bg-[#64a51b] h-12">
            <img
              src={uptrendImg}
              height="30px"
              width="30px"
              className="animate-pulse mx-auto mt-2"
              alt="sales"
            />
          </span>
          <span className="ml-2">
            1200 <br />
            Total Sales
          </span>
        </div>
        <div className="w-1/4 rounded-md mx-2 flex items-center overflow-hidden border border-[#64a51b] text-[#64a51b]">
          <span className="w-16 bg-[#64a51b] h-12">
            <img
              src={campaignImg}
              height="30px"
              width="30px"
              className="animate-pulse mx-auto mt-2"
              alt="campaign"
            />
          </span>
          <span className="ml-2">
            1225 <br />
            Total Campaigns
          </span>
        </div>
      </div>
      <div className="p-4 flex flex-row items-center justify-between mt-10">
        <div className="w-1/2">
          <h3 className="text-center text-xl font-semibold my-3 text-[#64a51b]">
            Users Data
          </h3>
          <Users />
        </div>
        <div className="w-1/2">
          <h3 className="text-center text-xl font-semibold my-3 text-[#64a51b]">
            Sales Data
          </h3>
          <Sales />
        </div>
      </div>
    </div>
  );
}

function Users() {
  const [data, setData] = useState([]);
  useEffect(() => {
    (async () => {
      const response = await axios.get("/api/member/getchart");
      // console.log(response);
      if (response.status == 200) {
        setData(response.data);
      }
    })();
  }, []);
  return (
    <AreaChart
      width={500}
      height={400}
      data={data}
      margin={{
        top: 10,
        right: 30,
        left: 0,
        bottom: 0,
      }}
    >
      <CartesianGrid strokeDasharray="3 3" />
      <XAxis dataKey="date" />
      <YAxis />
      <Tooltip />
      <Area type="monotone" dataKey="users" stroke="#64a51b" fill="#64a51b" />
    </AreaChart>
  );
}

function Sales() {
  const [data, setData] = useState([]);
  useEffect(() => {
    (async () => {
      const response = await axios.get("/api/payment/getchart");
      // console.log(response);
      if (response.status == 200) {
        setData(response.data);
      }
    })();
  }, []);
  return (
    <AreaChart
      width={500}
      height={400}
      data={data}
      margin={{
        top: 10,
        right: 30,
        left: 0,
        bottom: 0,
      }}
    >
      <CartesianGrid strokeDasharray="3 3" />
      <XAxis dataKey="date" />
      <YAxis />
      <Tooltip />
      <Area type="monotone" dataKey="sales" stroke="#64a51b" fill="#64a51b" />
    </AreaChart>
  );
}
